@import "../bower_components/breakpoint-sass/stylesheets/breakpoint";

.adminimal {
	overflow-x: hidden;

	.page-title {
		color: #fff;
		font-size: 2.35em;
		font-weight: 300;
		line-height: 1.2em;
		margin-bottom: 1rem;
	}

	.responsive-table {
		overflow-x: scroll;
	}

	.heading-a {
		color: #fff;
		font-size: 2.35em;
		font-weight: 300;
		line-height: 1.2em;
		margin-bottom: 1rem;
	}

	.content-header {
		background-color: #434343;
		padding: 2rem 0 0;
	}

	.layout-container {
		max-width: 1500px;

		@media screen and (min-width: 1500px) {
			margin: auto;
		}
	}

	thead {
		th {
			border: none;
			color: #909090;
			font-size: 0.91rem;
			font-weight: 500;
			text-transform: none;

			&.is-active {
				background: #fff;
				border: 1px solid #e6e4df;
				border-bottom: 1px solid #fff;
			}
		}

		>tr {
			border-bottom: 1px solid #e6e4df;
		}
	}

	th {
		>a {
			&:hover {
				text-decoration: underline;
			}

			&:after {
				display: none;
			}
		}

		&.is-active>a {
			color: #333;

			&:hover {
				color: #333;
			}

			&:after {
				display: none;
			}
		}
	}

	.tabs__tab {
		border: 1px solid transparent;
		background: transparent;

		&:hover {
			background: transparent;
		}

		&:focus {
			background: transparent;
		}
	}

	.is-collapse-enabled {
		.tabs__trigger {
			font-size: 2em;
			border-radius: 0;
			box-sizing: border-box;
		}

		.tabs:before {
			display: none;
		}
	}

	.is-horizontal {
		.tabs__tab {
			transition: all, 0.3s;

			&.is-active {
				border-radius: 0;
				border: none;
				margin-left: 0;
				margin-right: 0;
			}

			+.tabs__tab {
				border-radius: 0;
				border: none;
				margin-left: 0;
				margin-right: 0;
			}
		}

		.tabs {
			&.primary {
				.tabs__tab {
					&:first-child {
						border-radius: 0;
						border: none;
						margin-left: 0;
						margin-right: 0;

						&.is-active {
							box-shadow: -0.5rem 0 0 #fff;
						}
					}

					&:last-child {
						border-radius: 0;
						border: none;
						margin-left: 0;
						margin-right: 0;
					}

					&.is-active {
						border-radius: 0;
						border: none;
						margin-left: 0;
						margin-right: 0;
					}
				}

				a:hover {
					text-decoration: underline;
				}
			}

			&:before {
				display: none;
			}
		}

		ul.tabs.secondary li:first-of-type {
			margin-left: 0;
		}
	}

	.vertical-tabs {
		background: #f2f2f0;

		border-radius: 0;
		border: 1px solid #b5b5b5;
	}

	.vertical-tabs__panes {
		border: none;
	}

	.vertical-tabs__menu {
		border-bottom: none;
	}

	.button {
		background-color: #f9f9f9;

		border-radius: 0;

		text-shadow: none;

		transition: all, 0.2s;

		&:hover {
			background-color: #ffffff;
			border-color: #008ee6;
		}

		&:focus {
			background-color: #ffffff;
			border-color: #008ee6;

			box-shadow: none;
			border-color: rgb(104, 163, 207);
			outline: rgb(193, 224, 255) solid 2px;
		}
	}

	.action-links {
		margin: 2em 0;

		.button--primary {
			background: #fff;
			border: 2px solid #327ebd;

			border-radius: 999px;
			color: #327ebd;
			font-family: sans-serif;
			font-size: 14px;
			font-weight: 600;
			padding: 0.4rem 1.33rem;

			&:hover {
				background-color: #327ebd;
				background-image: none;

				box-shadow: none;
				color: #fff;
			}

			&:focus {
				background-color: #327ebd;
				background-image: none;

				box-shadow: none;
				color: #fff;
				border-color: #08639b;
				outline: none;
			}
		}
	}

	.button--small {
		background: #fff;
		border-color: #cacaca;

		border-radius: 0.2rem;
		font-weight: 500;

		&:focus {
			outline: none;
		}
	}

	.button--primary {
		background: #0071b8;
		border-color: #0071b8;

		&:hover {
			background-image: none;
			background-color: #1f86c7;
			border-color: #1f86c7;
		}

		&:focus {
			background-image: none;
			background-color: #1f86c7;
			border-color: #1f86c7;
		}
	}

	.button--danger {
		text-decoration: none;
		background-color: transparent;

		&:hover {
			text-decoration: underline;
		}
	}

	:has(> table) {
		overflow: auto;
	}

	select {
		min-height: 28px;
		text-shadow: none;

		@media screen and (max-width: 600px) {
			width: auto;
		}
	}

	input[type="number"]::-webkit-inner-spin-button,
	input[type="number"]::-webkit-outer-spin-button {
		opacity: 1;
	}

	input.form-autocomplete,
	input.form-text,
	input.form-tel,
	input.form-email,
	input.form-url,
	input.form-search,
	input.form-number,
	input.form-color,
	input.form-file,
	input.form-date,
	input.form-time,
	f textarea.form-textarea {
		background-color: white;
	}

	.form-type-textfield,
	.form-type-number,
	.form-type-select,
	.form-type-email,
	.form-type-password,
	.form-type-tel {
		display: flex;
		flex-flow: row wrap;
		align-items: baseline;

		input {
			flex: 1 1 calc(100% - var(--labelWidth));
			min-width: 150px;
		}
	}

	.form-type-password {
		label {
			width: 122px;
		}
	}

	label,
	.label {
		margin: 0 0.5em 0.2em 0.2em;
		display: inline-block;
		text-transform: capitalize;
	}

	input {
		&.form-autocomplete {
			box-shadow: none;

			border-radius: 0;
		}

		&.form-text {
			box-shadow: none;

			border-radius: 0;
			width: 100%;
		}

		&.form-tel {
			box-shadow: none;

			border-radius: 0;
		}

		&.form-email {
			box-shadow: none;

			border-radius: 0;
		}

		&.form-url {
			box-shadow: none;

			border-radius: 0;
		}

		&.form-search {
			box-shadow: none;

			border-radius: 0;
		}

		&.form-number {
			box-shadow: none;

			border-radius: 0;
		}

		&.form-color {
			box-shadow: none;

			border-radius: 0;
		}

		&.form-file {
			box-shadow: none;

			border-radius: 0;
		}

		&.form-date {
			box-shadow: none;

			border-radius: 0;
		}

		&.form-time {
			box-shadow: none;

			border-radius: 0;
		}
	}

	#edit-advanced {
		@include breakpoint(1200px) {
			&.sticky-panel {
				position: fixed;
				margin-top: 1em;
				margin-right: 1em;
				transition: 0.5s;
			}
		}
	}

	#user-form {
		label[for] {
			display: inline-block;

			&.option {
				display: inline;
			}

			&:after {
				content: ":\00a0";
				display: inline-block;
				vertical-align: baseline;
			}

			&.form-required:after {
				width: 12px;
				margin-right: 0.3em;
				margin: 0;
				background-position-x: right;
			}
		}

		.form-type-checkbox {
			label:after {
				content: "";
			}
		}

		.field--type-datetime .label:after {
			content: ":\00a0";
			display: inline;
		}

		.form-type-textfield,
		.form-type-select,
		.form-type-email,
		.form-type-password,
		.form-type-tel,
		.field--type-datetime {
			display: flex;
			flex-flow: row wrap;
			align-items: baseline;

			input {
				max-width: 100%;
			}

			.description {
				flex: 1 100%;
			}
		}

		#edit-group-full-name {

			.field--name-field-prefix,
			.field--name-field-suffix {
				display: inline-block;
			}

			#edit-field-mhcc-position {
				column-width: 200px;
			}
		}

		#edit-group-user {

			.form-type-textfield,
			.form-type-select,
			.form-type-email,
			.field--type-datetime {

				label,
				.label {
					flex: 0 0 calc(91px + 0.4em);
				}

				input {
					flex: 1 1 calc(100% - 91px - 2em);
				}
			}
		}

		#edit-group-website-user-information {
			#edit-status--wrapper {
				.fieldset-legend:after {
					content: ":\00a0";
					display: inline-block;
					vertical-align: baseline;
				}

				.form-item-status label:after {
					display: none;
				}
			}

			#edit-roles {
				display: block;
			}
		}

		#edit-group-user-login-info {

			.form-type-textfield,
			.form-type-select,
			.form-type-email,
			.form-type-password,
			.field--type-datetime {

				label,
				.label {
					flex: 0 0 calc(122px + 0.4em);
				}

				input {
					flex: 1 1 calc(100% - 122px - 2em);
				}
			}
		}

		[id*="edit-field-mailing-address-wrapper"] {

			.address-line1,
			.address-line2 {
				flex: 100% !important;
			}

			div[class*="address-line1"] {
				text-transform: capitalize;
			}

			div[class*="address-administrative-area"],
			div[class*="address-postal-code"] {
				display: inline-flex;
			}

			div[class*="address-postal-code"] {
				text-transform: capitalize;

				input {
					width: 7em;
				}
			}
		}

		#group-address {

			div[class*="address-country-code"],
			div[class*="address-locality"],
			div[class*="address-administrative-area"],
			div[class*="address-postal-code"] {

				label,
				.label {
					flex: 0 0 calc(60px + 0.4em);
				}

				input {
					flex: 1 1 calc(100% - 60px - 2em);
				}
			}

			div[class*="address-address-line1"],
			div[class*="address-address-line2"] {
				input {
					flex: 100%;
					width: 100%;
				}
			}
		}

		#edit-group-phone-numbers {
			input.form-tel {
				width: 9em;
				min-width: unset;
			}

			.field--name-field-home-phone,
			.field--name-field-cell-phone {
				display: inline-block;
				margin-right: 0.5em;
			}
		}

		#group-phone {

			div[class*="subform-field-phone-number"],
			div[class*="subform-field-phone-type"] {

				label,
				.label {
					flex: 0 0 calc(103px + 0.4em);
				}
			}
		}

		#edit-group-source {

			.form-item-field-source,
			.field--name-field-source-date {

				label,
				.label {
					flex: 0 0 83px;
				}

				input {
					flex: 1 1 calc(100% - 83px);
				}
			}

			div[class*="inline-entity-form-field-gift-card"],
			div[class*="inline-entity-form-field-amount"],
			div[class*="inline-entity-form-field-note-date-wrapper"] {
				display: flex;
				flex-flow: row wrap;
				align-items: baseline;

				label,
				.label {
					flex: 0 0 69px;
				}
			}

			.form-type-number .field-prefix {
				margin-right: 0.2em;
			}
		}

		#edit-group-allow-public-sharing-of-in {
			.field--name-field-license-plate input {
				width: 8em;
				min-width: unset;
			}
		}

		.payment-dates {
			display: flex;
			flex-flow: row wrap;
			align-items: start;

			>div {
				flex: 1 1 0;
				padding: 0 0.5em;
				min-width: 250px;
			}
		}

		#edit-status {
			display: flex;
			flex-flow: row wrap;

			.form-item {
				margin-right: 2em;
			}
		}

		#edit-roles {
			display: flex;
			flex-flow: row wrap;

			.form-item {
				margin-right: 1em;
				flex: 0 0 143px;

				&.form-type-textfield {
					display: inline-block;
				}
			}
		}

		.field--name-user-picture {
			.image-widget {
				display: flex;
				flex-flow: row wrap;
				justify-content: center;

				.image-preview {
					flex: 0 0 144px;
				}

				.image-widget-data {
					flex: 1 1 calc(100% - 154px);

					input.form-text {
						width: 100%;
						max-width: 450px;
					}
				}
			}
		}

		#group-phone,
		#group-address {
			padding: 18px;
			overflow: auto;

			.fieldset-wrapper {
				display: flex;
				flex-flow: column nowrap;
				margin: 0 -1em;

				.description {
					order: -1;
					padding: 0 1em;
				}
			}

			.paragraphs-subform {
				margin-left: -50px;
				margin-right: 5px;
			}
		}

		.paragraphs-add-wrapper {
			text-align: center;
		}

		.tabledrag-toggle-weight {}
	}

	.toolbar {
		.toolbar-tray-vertical {
			border: none;

			box-shadow: none;

			a {
				transition: all, 0.2s;
			}

			.level-2 {
				background: rgba(255, 255, 255, 0.03);
			}

			.level-3 {
				background: rgba(255, 255, 255, 0.05);
			}

			.level-4 {
				background: rgba(255, 255, 255, 0.07);
			}
		}

		.toolbar-tray-horizontal {
			border-bottom: 0;

			box-shadow: none;

			.menu-item {
				transition: all, 0.2s;
			}

			.toolbar-toggle-orientation {
				border: none;
			}

			ul li.menu-item {
				border: none;

				a {
					&:hover {
						text-decoration: none;
					}

					&.is-active:hover {
						text-decoration: underline;
					}
				}
			}
		}

		.toolbar-bar {
			background-color: #2d2d2d;

			box-shadow: none;

			.toolbar-tab {
				&:not(.workspaces-toolbar-tab)>.toolbar-item {
					color: #bebebe;
					font-weight: 600;

					&:hover {
						background-color: #2d2d2d;
						background-image: none;
						color: #fff;
						text-decoration: none;
					}
				}

				>.toolbar-item {
					&.is-active {
						background-image: none;
						background-color: #333;
						text-decoration: none;
					}

					&:focus {
						background-image: none;
						background-color: #333;
						text-decoration: none;
					}
				}
			}
		}

		.toolbar-menu {
			background: #333;

			.toolbar-menu {
				a {
					color: #bbb;

					&.is-active {
						background-color: #327ebd;
						color: #fff;
						text-decoration: none;

						&:hover {
							text-decoration: underline;
						}
					}
				}

				.toolbar-icon.toolbar-handle {
					&:before {
						background-image: url(../images/chevron-disc-down.svg);
					}

					&:hover:before {
						background-image: url(../images/chevron-disc-down-hover.svg);
					}

					&.open:before {
						background-image: url(../images//chevron-disc-up.svg);
					}
				}
			}

			a {
				color: #bbb;

				&:hover {
					background-color: #327ebd;
					color: #fff;
					text-decoration: none;
				}
			}

			ul ul {
				border: none;
			}
		}

		.toolbar-tray {
			background-color: #333;

			box-shadow: -2px 2px 12px rgba(0, 0, 0, 0.85);

			.menu-item--active-trail>.toolbar-box a {
				color: #bbb;
				font-weight: 500;
				text-decoration: none;
			}

			a.is-active {
				color: #bbb;
				font-weight: 500;
				text-decoration: none;
			}
		}

		.toolbar-toggle-orientation {
			background-color: #333;
		}

		.menu-item+.menu-item {
			border: 0 none;
		}

		.toolbar-icon.toolbar-handle {
			&:focus {
				background-color: transparent;
			}

			&:before {
				background-image: url(../images/chevron-disc-down.svg);
			}

			&:hover:before {
				background-image: url(../images/chevron-disc-down-hover.svg);
			}

			&.open:before {
				background-image: url(../images//chevron-disc-up.svg);
			}
		}

		a.is-active:hover {
			text-decoration: underline;
		}
	}

	.toolbar-tray-horizontal {
		.menu-item--expanded {
			background: #333;
		}

		a:focus {
			background: transparent;
		}

		ul ul li.menu-item:first-child {
			border-top: 1px solid #555;
		}
	}

	.ui-dialog {
		outline: none;

		box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);

		.ui-dialog-titlebar {
			border-radius: 0;
			background: #333;
			outline: none;
		}

		.ui-dialog-titlebar-close {
			opacity: 0.5;

			transition: all, 0.2s;

			&:hover {
				opacity: 1;
			}
		}

		.ui-widget-content.ui-dialog-buttonpane {
			border-radius: 0;
		}
	}

	.messages {
		margin: 1rem 0;
		border-width: 1px 0 1px 0;

		border-radius: 0;
		background-color: transparent;

		box-shadow: none;
		background-size: 1.3rem;
		background-position: 5px 50%;
	}

	.messages--error {
		border-color: #e32700;
	}

	.messages--warning {
		border-color: #e29700;
	}

	.messages--status {
		border-color: #73b355;
	}

	.cke_top {
		background: #f7f7f7;
		background-image: none;

		box-shadow: none;
	}

	.cke_bottom {
		background: #f7f7f7;
		background-image: none;

		box-shadow: none;
	}

	.filter-wrapper {
		background: #f7f7f7;
	}

	.cke_toolgroup {
		border-radius: 0;
	}

	.toolbar-icon-system-admin-content {
		&:active:before {
			background-image: url(/core/themes/stable/images/core/icons/787878/file.svg);
		}

		&.is-active:before {
			background-image: url(/core/themes/stable/images/core/icons/787878/file.svg);
		}
	}

	.toolbar-icon-system-admin-structure {
		&:active:before {
			background-image: url(/core/themes/stable/images/core/icons/787878/orgchart.svg);
		}

		&.is-active:before {
			background-image: url(/core/themes/stable/images/core/icons/787878/orgchart.svg);
		}
	}

	.toolbar-icon-system-themes-page {
		&:active:before {
			background-image: url(/core/themes/stable/images/core/icons/787878/paintbrush.svg);
		}

		&.is-active:before {
			background-image: url(/core/themes/stable/images/core/icons/787878/paintbrush.svg);
		}
	}

	.toolbar-icon-entity-user-collection {
		&:active:before {
			background-image: url(/core/themes/stable/images/core/icons/787878/people.svg);
		}

		&.is-active:before {
			background-image: url(/core/themes/stable/images/core/icons/787878/people.svg);
		}
	}

	.toolbar-icon-system-modules-list {
		&:active:before {
			background-image: url(/core/themes/stable/images/core/icons/787878/puzzlepiece.svg);
		}

		&.is-active:before {
			background-image: url(/core/themes/stable/images/core/icons/787878/puzzlepiece.svg);
		}
	}

	.toolbar-icon-system-admin-config {
		&:active:before {
			background-image: url(/core/themes/stable/images/core/icons/787878/wrench.svg);
		}

		&.is-active:before {
			background-image: url(/core/themes/stable/images/core/icons/787878/wrench.svg);
		}
	}

