/********************* LAYOUTS *********************/
.layout {
	position: relative;
	margin-bottom: 1em;
	justify-content: space-between;

	.layout__region {
		padding: 0.5em 0;
		margin: 0;

		&:first-child {
			margin-top: 0;
		}

		&:last-child {
			margin-bottom: 0;
		}

		@include breakpoint(740px) {
			padding: 0 0.5em;
			margin: 0;

			&:first-child {
				margin-left: 0;
			}

			&:last-child {
				margin-right: 0;
			}
		}
	}

	&.layout--twocol-section--50-50 {
		>.layout__region {
			@include breakpoint(40em) {
				flex: 1 1 100%;
			}

			@include breakpoint(740px) {
				flex: 0 1 calc(50% - 0.5em);
			}
		}
	}

	&.layout--twocol-section.layout--twocol-section--75-25 {
		flex-flow: row wrap;

		@include breakpoint(900px) {
			flex-flow: row nowrap;
		}

		>.layout__region--first,
		>.layout__region--second {
			flex: 1 1 100%;
		}

		>.layout__region--first {
			@include breakpoint(900px) {
				flex: 0 1 75%;
			}
		}

		>.layout__region--second {
			@include breakpoint(900px) {
				flex: 0 1 25%;
			}
		}
	}

	&.layout--twocol {
		flex-flow: row wrap;

		@include breakpoint(740px) {
			flex-flow: row nowrap;
		}

		>.layout__region--first {
			margin-bottom: 1em;
		}

		>.layout__region--first,
		>.layout__region--second {
			flex: 1 1 100%;

			@include breakpoint(40em) {
				flex: 1 1 100%;
			}

			@include breakpoint(740px) {
				flex: 1 1 calc(50% - 0.5em);
				max-width: calc(50% - 0.5em);
			}
		}

		>.layout__region--first {
			@include breakpoint(740px) {
				margin-left: 0;
				margin-bottom: 0;
			}
		}

		>.layout__region--second {
			@include breakpoint(740px) {
				margin-right: 0;
			}
		}
	}

	&.layout--twocol-bricks {
		display: flex;
		flex-wrap: wrap;

		>.layout__region {
			flex: 0 1 100%;
			box-sizing: border-box;
		}

		@include breakpoint(40em) {

			>.layout__region--first-above,
			>.layout__region--second-above,
			>.layout__region--first-below,
			>.layout__region--second-below {
				flex: 0 1 50%;
				max-width: 49%;
			}

			>.layout__region--first-above,
			>.layout__region--first-below {
				margin-right: 1%;
			}

			>.layout__region--second-above,
			>.layout__region--second-below {
				margin-left: 1%;
			}
		}
	}

	&.layout--threecol-section--33-34-33 {

		>.layout__region--first,
		>.layout__region--third {
			@include breakpoint(40em) {
				flex: 0 1 calc(33% - 0.5em);
			}
		}

		>.layout__region--second {
			@include breakpoint(40em) {
				flex: 0 1 calc(34% - 1em);
			}
		}
	}

	&.layout--threecol-section--25-50-25 {

		>.layout__region--first,
		>.layout__region--third {
			@include breakpoint(40em) {
				flex: 0 1 calc(25% - 0.5em);
			}
		}

		>.layout__region--second {
			@include breakpoint(40em) {
				flex: 0 1 calc(50% - 1em);
			}
		}
	}
}

